.main {
    background-color: #eafbff;
    background-image: linear-gradient(to bottom,
            #eafbff 0%,
            #eafbff 50%,
            #5290f9 50%,
            #5290f9 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    overflow: hidden;
    margin: 0;
}


.main nav {
    background-color: #fff;
    padding: 20px;
    width: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
    /* box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3); */
    transition: width 0.6s linear;
    overflow-x: hidden;
    overflow-y: hidden;

}

.main nav.active {
    width: 350px;
}

.main nav ul {
    display: flex;
    list-style-type: none;
    padding: 0;
    margin: 0;
    width: 0;
    transition: width 0.6s linear;
}

.main nav.active ul {
    width: 100%;
}

.main nav ul li {
    transform: rotateX(0deg);
    opacity: 0;
    transition: transform 0.6s linear, opacity 0.6s linear;
}

.main nav.active ul li {
    opacity: 1;
    transform: rotate(360deg);
}

.main nav ul a {
    position: relative;
    color: #000;
    text-decoration: none;
    margin: 0 10px;
}

.icon {
    background-color: #fff;
    border: 0;
    cursor: pointer;
    padding: 0;
    position: relative;
    height: 30px;
    width: 30px;
}

.icon .line {
    background-color: #5290f9;
    height: 2px;
    width: 20px;
    position: absolute;
    top: 10px;
    left: 5px;
    transition: transform 0.6s linear;
}

.icon .line2 {
    top: auto;
    bottom: 10px;
}


.main nav.active .icon .line1 {
    transform: rotate(-765deg) translateY(5.5px);
}

.main nav.active .icon .line2 {
    transform: rotate(765deg) translateY(-5.5px);
}