.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.navBtn {
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-size: 20px;
}

.openBtn {
    position: fixed;
    top: 10px;
    left: 10px;
}

.nav {
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out;
}

.nav.visible {
    transform: translateX(0);
}

.navBlack {
    background-color: rgb(34, 31, 31);
    width: 60%;
    max-width: 480px;
    min-width: 320px;
    transition-delay: 0.4s;
}

.navBlack.visible {
    transition-delay: 0s;
}

.navRed {
    background-color: rgb(229, 9, 20);
    width: 95%;
    transition-delay: 0.2s;
}

.navRed.visible {
    transition-delay: 0.2s;
}

.navWhite {
    background-color: #fff;
    width: 95%;
    padding: 40px;
    position: relative;
    transition-delay: 0s;
}

.navWhite.visible {
    transition-delay: 0.4s;
}

.closeBtn {
    opacity: 0.3;
    position: absolute;
    top: 40px;
    right: 30px;
}

.list {
    list-style-type: none;
    padding: 0;
}

.list li {
    margin: 20px 0;
}

.list li a {
    color: rgb(34, 31, 31);
    font-size: 14px;
    text-decoration: none;
    text-transform: uppercase;
}

.list ul{
    list-style-type: none;
    padding-left: 20px;
}